import React, { type FC } from 'react';
import {
  Button,
  Dropdown,
  TextInput,
  NumberInput,
  type ChangeHandler,
} from 'oojs-ui-react';

/**  */
export const searchCodes = {
  none: '',
  insource: '',
  prefix: '',
  intitle: '',
  incategory: '',
  linksto: '',
  hastemplate: '',
  contentmodel: '',
  subpageof: '',
  filetype: '',
  filemime: 'MIME',
  filesize: '',
  filewidth: '',
  fileheight: '',
  fileres: '',
  filebits: '',
};

export const contentModels = [
  'GadgetDefinition',
  'sanitized-css',
  'Scribunto',
  'wikitext',
  'javascript',
  'json',
  'css',
  'text',
];

/**  */
export type SearchCode = keyof typeof searchCodes;

/**  */
export interface LineValue {
  /**  */
  code: SearchCode;
  /**  */
  value: string | number | undefined;
}

/**  */
export interface Condition extends LineValue {
  index: number;
}

interface ConditionLineProps extends Omit<Condition, 'index'> {
  onChange?: ChangeHandler<LineValue>;
  /**  */
  onRemove: () => void;
  /**  */
  onFocus?: () => void;
  /**  */
  nextLine?: boolean;
}

/**  */
const ConditionLine: FC<ConditionLineProps> = ({
  code,
  value,
  onChange,
  onRemove,
  onFocus,
  nextLine = false,
}) => {
  const identifyType = (searchCode: SearchCode) => {
    switch (searchCode) {
      case 'filewidth':
      case 'fileheight':
      case 'filebits':
        return 'number';
      case 'contentmodel':
        return 'contentmodel';
      default:
        return 'text';
    }
  };

  const selectType = identifyType(code);

  /** value */
  const handleCodeChange: ChangeHandler<SearchCode> = (data) => {
    if (!onChange) {
      return;
    }
    let newValue = value;
    if (identifyType(data.value) === 'contentmodel' && !contentModels.includes(value as string)) {
      // value
      newValue = '';
    } else if (identifyType(data.value) === 'number' && identifyType(code) === 'text') {
      // value
      if (Number.isFinite(Number(value))) {
        newValue = Number(value);
      } else {
        newValue = void 0;
      }
    }
    onChange({
      value: { code: data.value, value: newValue },
      oldValue: { code, value },
    });
  };

  const handleValueChange: ChangeHandler<any> = (data) => {
    if (onChange) {
      onChange({
        value: { code, value: data.value },
        oldValue: { code, value },
      });
    }
  };

  return (
    <div className={`condition-line ${nextLine ? 'condition-line-next' : ''}`}>
      <Dropdown
        className='condition-code'
        value={code}
        onChange={handleCodeChange}
        onFocus={onFocus}
        options={Object.entries(searchCodes).map(([searchCode, searchText]) => ({
          key: searchCode,
          data: searchCode,
          children: searchText,
        }))}
      />
      {selectType === 'text' && (
        <TextInput
          className='condition-text'
          value={value as string}
          onChange={handleValueChange}
          onFocus={onFocus}
        />
      )}
      {selectType === 'number' && (
        <NumberInput
          className='condition-text'
          value={value as number}
          onChange={handleValueChange}
          onFocus={onFocus}
        />
      )}
      {selectType === 'contentmodel' && (
        <Dropdown
          className='condition-text'
          value={value as string}
          onChange={handleValueChange}
          onFocus={onFocus}
          options={contentModels.map((model) => ({
            key: model,
            data: model,
            children: model,
          }))}
        />
      )}
      <Button
        className='condition-removebutton'
        icon='subtract'
        tabIndex={-1}
        onClick={onRemove}
        disabled={nextLine}
      />
    </div>
  );
};

export default ConditionLine;
